import React, { useContext } from "react";
import { Helmet } from "react-helmet";
import { Link, useNavigate } from "react-router-dom";
import "../Register/Register";
import videoFile from "../../assets/yaser.mp4";
import { motion } from "framer-motion";
import { fadeIn } from "../../variants";
import * as Yup from "yup";
import { useFormik } from "formik";
import { UserContext } from "../../context/UserContext";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export default function Login() {
  const { sendDataToLogin, setToken } = useContext(UserContext);

  const navigate = useNavigate();

  const validationSchema = Yup.object({
    email: Yup.string()
      .required("email is required")
      .email("Please check your email"),
    password: Yup.string()
      .required("password is required")
  });
  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    validationSchema: validationSchema,
    onSubmit: async (values) => {

      try {
        const response = await sendDataToLogin(values);
        if (response.status === 200) {

          localStorage.setItem("token", `${response.data.token}`);
          setToken(response.data.token)
          navigate("/");
        }
      } catch (error) {
        console.error("Error during login:", error);
        toast.error("Login failed. Please check your Password or email.");
      }
    },
  });

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Login</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <div className="container d-flex justify-content-center align-items-center min-vh-100">
        <motion.div
          variants={fadeIn("right", 0.2)}
          initial="hidden"
          animate="show"
          exit="hidden"
          className="row border w-75 rounded-5 p-3 bg-white shadow  "
        >
          <div className="slider col-md-6 rounded-4 d-flex justify-content-center blue-color3 align-items-center flex-column left-box">
            <motion.div
              variants={fadeIn("down", 0.4)}
              initial="hidden"
              animate="show"
              exit="hidden"
              className="mb-5 d-flex"
            >
              <video
                src={videoFile}
                className="w-75 m-auto rounded"
                autoPlay
                muted
                playsInline
                loop
              />
            </motion.div>
            <motion.div
              className="text-center"
              variants={fadeIn("up", 0.4)}
              initial="hidden"
              animate="show"
              exit="hidden"
            >
              <p className="text-white fs-2">Be Verified</p>
              <small className="text-white text-wrap text-center mb-3">
                Join experienced Designers on this platform.
              </small>
            </motion.div>
          </div>

          <form className="col-md-6 right-box" onSubmit={formik.handleSubmit}>
            <div className="row align-items-center">
              <div className="header-text headerHello mb-4">
                <h2>Hello,Again</h2>
                <p>We are happy to have you back.</p>
              </div>
              <div className="mb-3">
                <input
                  className="form-control emailInput"
                  type="email"
                  placeholder="Email"
                  id="email"
                  name="email"
                  value={formik.values.email}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                />
                {formik.errors.email && formik.touched.email ? (
                  <div className="alert alert-danger">
                    {formik.errors.email}
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="pass-field mb-5 position-relative">
                <input
                  className="form-control passwordInput"
                  type="password"
                  placeholder="Password"
                  id="password"
                  name="password"
                  value={formik.values.password}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                />
                {formik.errors.password && formik.touched.password ? (
                  <div className="alert alert-danger">
                    {formik.errors.password}
                  </div>
                ) : (
                  ""
                )}
              </div>
              <p className="text-danger showUpLog" />
              <div className="mb-3">
                <button
                  type="submit"
                  className=" form-control blue-color text-white w-100 fs-6 loginBtn"
                  disabled={!(formik.isValid && formik.dirty)}
                >
                  Login
                </button>
              </div>
              <div className="row">
                <small className="signUpMassege">
                  Don't have account?
                  <Link to={"/register"} className="signView">
                    Sign Up
                  </Link>
                </small>
              </div>
            </div>
          </form>
        </motion.div>
      </div>
    </>
  );
}
